a {
    text-decoration: none;
    color: white;
}

.navbar__container {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    height: 40px;
    font-family: Iskry;
    font-weight: 100;
    color: white;
    font-size: 30px;
    background-color: black;
    padding: 12px;
}
.positionFixed {
    position: fixed;
    width: 100%;
}

.navbar__image {
    width: 20vh;
}

.linksTest {
    display: flex;
    justify-content: space-around;
    width: 600px;
}

@media screen and (max-width: 766px) {
   .navbar__container {
    justify-content: space-evenly;
    font-size: 17px;
    padding: 0px;
   }
   .navbar__image {
       width: 13vh;
       margin-left: 10px;
   }
  }


  @media screen and (max-width: 912px) and (min-width: 900px ) {
    .navbar__section {
        font-size: 3vh;
    }
   }

   @media screen and (max-width: 280px) and (min-width: 200px ) {
    .navbar__section {
        display: inline !important;
        z-index: 9999 !important;
        left: 0 !important;
        position: absolute;
     }
   }
 



  